<template>
  <ResizableContainer className="userEditor--canvas-wrapper">
    <template #default="{ containerWidth }">
      <BannerCanvas
        v-if="bannerStatus === 'success'"
        :canvas="canvasItem"
        :scheme="banner?.schemes[0]?.id"
        :banner="banner"
        :zoom="containerWidth / canvasItem.width"
        rounded
      />
    </template>
  </ResizableContainer>
</template>

<script>
import BannerCanvas from '@/components/BannerCanvas.vue';
import ResizableContainer from '@/components/ResizableContainer/ResizableContainer.vue';
import { useBannersStore } from '@/stores/useBannersStore';

export default {
  setup() {
    return {
      bannersStore: useBannersStore(),
    };
  },
  computed: {
    banner() {
      return this.bannersStore.banner.data;
    },
    bannerStatus() {
      return this.bannersStore.banner.status;
    },
    canvasItem() {
      return this.bannersStore.canvases[0];
    },
  },
  components: { BannerCanvas, ResizableContainer },
};
</script>

<style lang="scss" scoped>
.userEditor {
  &--canvas {
    &-wrapper {
      display: flex;
      justify-content: center;
      padding: 9px;
      width: 500px;
      height: 500px;
      background-color: var(--color-white);
      border-radius: 15px;
    }
  }
}
</style>
