import api from '@/api';
import { defineStore } from 'pinia';

export const useGeneratedBannersStore = defineStore('generatedBanners', {
  state: () => ({
    generatedBanners: {
      data: [],
      status: '',
      error: '',
      oldParams: {
        description: null,
        offer: null,
        companyCategory: null,
        color: null,
        page: 0,
      },
    },
  }),
  getters: {
    banners() {
      return this.generatedBanners.data;
    },
    currentPage() {
      return this.generatedBanners.oldParams.page;
    },
    oldParams() {
      return this.generatedBanners.oldParams;
    },
  },
  actions: {
    async getGeneratedBanners() {
      this.generatedBanners.status = 'loading';
      try {
        const { data: generatedBanners } = await api.generatedBanners.get();

        this.generatedBanners.data = generatedBanners;
        this.generatedBanners.status = 'success';
        this.generatedBanners.error = null;
        return Promise.resolve();
      } catch (e) {
        this.generatedBanners.status = 'error';
        this.generatedBanners.error = e;
        return Promise.reject(e);
      }
    },
    async findGeneratedBanners(params, isLoadMore = false) {
      this.generatedBanners.status = 'loading';
      try {
        const filteredParams = Object.fromEntries(
          Object.entries(params).filter(
            // eslint-disable-next-line no-unused-vars
            ([_, value]) =>
              value !== '' && value !== null && value !== undefined,
          ),
        );

        this.generatedBanners.oldParams = { ...filteredParams };

        const { data: generatedBanners } =
          await api.generatedBanners.findGeneratedBanners(filteredParams);

        this.generatedBanners.data = isLoadMore
          ? [...this.banners, ...generatedBanners]
          : generatedBanners;
        this.generatedBanners.page = params.page;
        this.generatedBanners.status = 'success';
        this.generatedBanners.error = null;
        return Promise.resolve();
      } catch (e) {
        this.generatedBanners.status = 'error';
        this.generatedBanners.error = e;
        this.generatedBanners.data = [];
        return Promise.reject(e);
      }
    },
  },
});
