      <p>
        We're building something spectacular. <br />Please have a little faith
        and patience.
      </p>
      <span>If you're an admin</span>
      <AppButton @click="showModalSignIn" outlined color="black"
        >Sign in</AppButton
      >
    </div>
  </div>
</template>

<script>
import AppButton from '@/components/AppButton.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';

export default {
  name: 'HomeView',
  setup() {
    return {
      appStore: useAppStore(),
      userStore: useUserStore(),
    };
  },
  components: {
    AppButton,
  },
  methods: {
    showModalSignIn() {
      if (this.isAuth) {
        this.$router.push({ name: 'adminBanners' });
      } else {
        this.appStore.showModalAuth('signin');
      }
    },
  },
  computed: {
    isAuth() {
      return this.userStore.isAuthenticated;
    },
    isAdmin() {
      return this.userStore.isAdmin;
    },
  },
};
</script>

